"use client";
import { useEnquiryModal } from "./EnquiryModalContext";

export default function Hero() {
  const { open } = useEnquiryModal();
  return (
    <section className="bg-slate-100 pt-32 pb-20 md:pt-40 md:pb-28 overflow-hidden">
      <div className="container mx-auto px-6 grid md:grid-cols-2 gap-10 items-center">
        <div className="text-center md:text-left">
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-extrabold text-slate-900 leading-tight">
            Navigate Your Future.{" "}
            <span className="text-blue-600">Unlock Your Dream College.</span>
          </h1>
          <p className="mt-6 text-lg text-slate-600 max-w-lg mx-auto md:mx-0">
            Admission Dunia 2.0 provides expert guidance and personalized
            counseling to help you secure admission into top universities
            worldwide.
          </p>
          <div className="mt-8">
            <button
              onClick={open}
              className="inline-block bg-blue-600 text-white px-8 py-4 rounded-lg text-lg font-semibold hover:bg-blue-700 transition-transform hover:scale-105 shadow-xl"
            >
              Get Free Counseling
            </button>
          </div>
        </div>
        <div className="hidden md:flex items-center justify-center h-full relative">
          <div className="w-full h-full min-h-[400px] relative">
            {/* Floating bubbles with remote placeholders */}
            <div
              className="bubble absolute top-[5%] left-[40%] w-24 h-24 bg-white rounded-full shadow-lg p-2 flex items-center justify-center"
              style={{ animationDelay: "0s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=IIT+B" alt="College Logo A" className="rounded-full w-full h-full object-contain" />
            </div>
            <div
              className="bubble absolute top-[25%] left-[65%] w-32 h-32 bg-white rounded-full shadow-xl p-3 flex items-center justify-center"
              style={{ animationDelay: "-2s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=AIIMS" alt="College Logo B" className="rounded-full w-full h-full object-contain" />
            </div>
            <div
              className="bubble absolute bottom-[25%] left-[50%] w-20 h-20 bg-white rounded-full shadow-md p-2 flex items-center justify-center"
              style={{ animationDelay: "-4s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=IIM+A" alt="College Logo C" className="rounded-full w-full h-full object-contain" />
            </div>
            <div
              className="bubble absolute bottom-[35%] right-[0%] w-28 h-28 bg-white rounded-full shadow-lg p-2.5 flex items-center justify-center"
              style={{ animationDelay: "-1.5s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=VIT" alt="College Logo D" className="rounded-full w-full h-full object-contain" />
            </div>
            <div
              className="bubble absolute top-[0%] right-[10%] w-16 h-16 bg-white rounded-full shadow-md p-1 flex items-center justify-center"
              style={{ animationDelay: "-3s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=BITS" alt="College Logo E" className="rounded-full w-full h-full object-contain" />
            </div>
            <div
              className="bubble absolute top-[55%] left-[30%] w-28 h-28 bg-white rounded-full shadow-lg p-2.5 flex items-center justify-center"
              style={{ animationDelay: "-5s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=KIIT" alt="College Logo F" className="rounded-full w-full h-full object-contain" />
            </div>
            <div
              className="bubble absolute bottom-[5%] left-[70%] w-24 h-24 bg-white rounded-full shadow-md p-2 flex items-center justify-center"
              style={{ animationDelay: "-6s" }}
            >
              <img src="https://placehold.co/100x100/FFFFFF/000000?text=CMC" alt="College Logo G" className="rounded-full w-full h-full object-contain" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
